/* -------------- stick -------------- */
.stick {
  position: relative;
  float: left;
  width: 200px;
  height: 5px;
}

.stick:after {
  content: '';
  color: #fff;
  font-size: 16px;
  position: absolute;
  width: 100%;
  height: 1px;
  line-height: 20px;
  left: 0;
  top: 23px;
}

.stick:before {
  content: '';
  position: absolute;
  background-color: #000;
  top: 23px;
  height: 1px;
  width: 0px;
  z-index: 0;
  transform-origin: 100% 0%;
  -webkit-animation: stick 7s ease-in-out infinite;
  animation: stick 7s ease-in-out infinite;
}

@-webkit-keyframes stick {
  0% {
    width: 0px;
    left: 0px;
  }
  48% {
    width: 100%;
    left: 0px;
  }
  50% {
    width: 100%;
    left: 0px;
  }
  52% {
    width: 100%;
    left: 0px;
  }
  100% {
    width: 0px;
    left: 0px;
  }
}

@keyframes stick {
  0% {
    width: 0px;
    left: 0px;
  }
  48% {
    width: 100%;
    left: 0px;
  }
  50% {
    width: 100%;
    left: 0px;
  }
  52% {
    width: 100%;
    left: 0px;
  }
  100% {
    width: 0px;
    left: 0px;
  }
}

