.work_explain {
  position: relative;
  list-style: none;
  margin: 0 100px;
  margin-top: 10%;
  margin-bottom: 18%;
  display: inline-block;
  width: 100%;
}
.arrow {
  width: 40px;
  height: 40px;
  position: absolute;
  bottom: 0px;
  left: 50%;
  margin-left: -120px;
  box-shadow: none;
  cursor: pointer;
  border-width: medium;
  border-width: initial;
  border-style: none;
  border-color: currentColor;
  border-color: initial;
  border-image: none;
  border-image: initial;
  outline: none;
  background: none;
  padding: 0px;
}
.arrow::after {
  content: '';
  position: absolute;
  bottom: 1px;
  left: 50%;
  width: 11px;
  height: 11px;
  transform: translateX(-50%) rotate(-45deg);
  border-left: 1px solid black;
  border-bottom: 1px solid black;
}
.arrow::before {
  content: '';
  position: absolute;
  width: 1px;
  height: 100%;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  background: black;
}
